/* Spinner.css */
.spinner-container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9999;
    display: none; /* Initially hidden */
  }
  
  .spinner-container.show {
    display: block; /* Show when 'show' class is added */
  }
  