.footer {
    text-align: center;
    font-weight: bold;
    background-color: #8731ce;
    color: white;
    padding: 4px 0;
    width: 100%;
    bottom: 0;
    position: fixed;
    box-shadow: #000000 4px -4px 8px;
    border-top-left-radius: 100%;
    
  }
  @media (max-width: 768px) {
    .footer {
      padding: 1%;
      font-size: 8px;
    }
  }
