.form-container {
        max-width: 600px;
        margin: 0 auto;
        padding: 20px;
        background-color: #f5f5f5;
        border: 1px solid #ddd;
        border-radius: 5px;
    }
.back-button {
    max-width: 645px;
    font-weight: bold;
    padding: 8px 14px;
    border: none;
    border-radius: 5px;
    margin: 0 auto;
    background-color: #8731ce;
    color: white;
    cursor: pointer;
    margin-bottom: 10px;
    font-size: medium;
  }
  
  .back-button:hover {
    opacity: 0.9;
    color: Black;
    background-color: #7f30c1;
  }
  .edit-button:hover {
    opacity: 0.9;
    color: Black;
    background-color: rgb(220, 145, 4);
  }
  .delete-button:hover {
    opacity: 0.9;
    color: Black;
    background-color: rgb(203, 2, 2);
  }
  .add-button:hover {
    opacity: 0.9;
    color: Black;
    background-color: #218838;
  }
.table-container {
    width: 100%;
    overflow-x: auto;
  }
  
  .fuel-table {
    width: 100%;
    border-collapse: collapse;
    table-layout: fixed;
  }
  
  .fuel-table th,
  .fuel-table td {
    padding: 10px;
    text-align: center;
    border: 1px solid #ddd;
  }
  
  .fuel-table th {
    background-color: #8731ce;
    color: white;
  }
  input,select,label{
    text-align: center;
  }
  

  button {
    font-weight: bold;
    padding: 8px 14px;
    border: none;
    border-radius: 5px;
    margin: 5px;
  }
  
  .edit-button {
    background-color: orange;
    color: white;
    width: 70%;
    margin: 0 auto;
  }
  
  .delete-button {
    background-color: red;
    color: white;
    width: 70%;
  }
  .add-button {
    background-color: #28a745;
    color: white;
    font-size: medium;
  }
  
  .totals{
    text-align: left;
    max-width: 100%;
    margin: 0 auto;
    padding: 20px;
    background-color: #f5f5f5;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: larger;
    padding-bottom: 100px;
    margin-top: 1px;
  }
  .para{
    padding-left: 5px;
    border: 2px solid rgb(103, 103, 103);
    width: 40%;
    margin: 0 auto;
    margin-bottom: 5px;
    font-weight: bold;
    border-radius: 5%;
  }
  .totals h3{
    text-align: center;
    font-size: 30px;
    margin-top: 5px;
    margin-bottom: 10px;
    text-shadow: #8731ce -4px 4px 8px;
    color: rgb(40, 39, 39);
  }
  .title{
    font-size: 30px;
    text-shadow: #8731ce -4px 4px 8px;
    border-radius: 10%;
    padding: 5px;
    color: rgb(40, 39, 39);
  }
  .litter{
    font-weight: bold;
    color: #8731ce;
  }
  .dollar{
    font-weight: bold;
    color: red;
  }
  .type{
    font-weight: bold;
    text-shadow: #8731ce -4px 4px 8px;
  }
  @media (max-width: 768px) {
    .form-container {
      width: 80%;
      margin: 0 auto;
      padding-right: 9%;
      
    }
  
    .fuel-table th, .fuel-table td {
      padding: 1px;
      font-size: 10px;
    }
  
    .totals {
      font-size: small;
      padding: 10px;
      margin-bottom: 10%;
    }
    .back-button {
      max-width: 50%;
      margin: 2% auto;
      margin-right: 25%;
      
      
  }
  .table-container {
    width: 100%;
  }
  
  .fuel-table {
    display: block;
    border: none;
    width: 100%;
    
  }
  input,select,label{
    text-align: left;
  }
  .full{
    text-align: center;
  }
  .edit-button {
    background-color: orange;
    color: white;
    width: 70%;
    margin: 0 auto;
    font-size: 7px;
    padding-left: 15%;
  }
  
  .delete-button {
    background-color: red;
    color: white;
    width: 70%;
    margin: 0 auto;
    font-size: 7px;
    padding-left: 1%;
  }
  .fuel-table th,
  .fuel-table td {
    font-size: 70%;
  }
  .para{
    padding-left: 5px;
    border: 2px solid rgb(103, 103, 103);
    width: 70%;
    margin: 0 auto;
    margin-bottom: 1%;
    font-weight: bold;
  }
  .add-button {
    background-color: #28a745;
    color: white;
    font-size: medium;
    margin-top: 5%;
  }
  .title{
    font-size: 150%;
    text-shadow: #8731ce -4px 4px 8px;
    border-radius: 10%;
    padding: 5px;
    padding-top: 5%;
    color: rgb(40, 39, 39);
  }
}
@media (max-width: 980px){
  .fuel-table th,
  .fuel-table td {
    font-size: 100%;
  }
}