.container {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f7f7f7;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
h2 {
  color: rgb(40, 39, 39);
}
.input-group {
  margin-bottom: 15px;
  margin-right: 5%;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.add-button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
  width: 50%;
  margin-top: 10px;
  font-weight: bold;
  margin-left: 25%;
}

.add-button:hover {
  background-color: #218838;
}

.back-button {
  background-color: #8731ce;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
  margin-top: 10px;
  font-weight: bold;
}

.back-button:hover {
  background-color: #7f30c1;
}
@media (max-width: 768px) {
  .container {
    width: 90%;
    padding: 10px;
    margin: 0 auto;
  }
}