.about-container {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    background-color: #f7f7f7;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  h2 {
    color: black;
    margin-bottom: 20px;
  }
  
  .about-container p {
    font-size: 1.1em;
    color: #666;
    margin-bottom: 15px;
  }
  
  .back-button {
    background-color: #8731ce;
    color: white;
    border: none;
    padding: 12px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
    font-weight: bold;
  }
  
  .back-button:hover {
    background-color: #7f30c1;
  }
  @media (max-width: 768px) {
    .about-container {
      width: 90%;
      padding: 10px;
      margin: 0 auto;
      margin-top: 10%;
    }
  
    .back-button {
      width: 100%;
    }
  }