.App-header {
    background-color: #8731ce;

    color: #f5f5f5;
    text-align: left; 
    width: 420px;
    border-bottom-right-radius: 90%;
    padding-top: 5px;
    box-shadow: #000000 -4px 4px 8px;
  }
  h1 {
    margin: 0;
    font-size: 40px;
    padding-left: 10px;
    padding-bottom: 25px;
    text-shadow: #000000 -4px 4px 8px;
  }
  h1:hover {
    font-size: 40.5px;
  }
  .header-link {
    text-decoration: none;
    color: inherit;
    display: block;
    width: 100%;
    height: 100%;
  }
  @media (max-width: 768px) {
    h1 {
      font-size: 24px;
      text-align: left;
    }
    .App-header {
      background-color: #8731ce;
  
      color: #f5f5f5;
      text-align: left; 
      width: 50%;
      border-bottom-right-radius: 90%;
      padding-top: 5px;
      box-shadow: #000000 -4px 4px 8px;
    }
    h1:hover {
      font-size: 24px;
    }
  }